@media screen and (max-width: 600px) {
  .element {
    font-size: 14px;
  }
}

@media screen and (min-width: 601px) and (max-width: 900px) {
  .element {
    font-size: 16px;
  }
}

@media screen and (min-width: 901px) {
  .element {
    font-size: 18px;
  }
}

.navbar {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #063070ec;
  color: #ffffff;
  padding-left: 15px;
  padding-top: 20px;
  padding-bottom: 20px;
}
.navbar-header-item {
  margin-right: 5%;
  margin-left: 5%;
}
.navbar-item-container {
  margin-left: 0px;
}
.navbar-button {
  float: right;
  margin-right: 30px;
  display: flex;
  gap: 20px;
}
