.calendarWrap {
  display: inline-block;
  position: relative;
  z-index: 10000 !important;
}
.date {
  position: absolute;
  /* position: fixed; */
  left: 100%;
  transform: translateX(-50%);
  top: 30px;
  border: 1px solid #ccc;
  z-index: 10000 !important;
}
.react-date-picker__calendar--open {
  top: 100% !important;
  bottom: unset !important;
}
.DateRange-inputBox {
  width: 170px;
  padding: 5px;
  opacity: 0.5;
}
