.Parent-container {
  /* border: 3px orange solid; */
  position: relative;
  height: 80px;
}
.Form-container {
  border: 3px orange solid;
  background-color: #ffffff;
  padding: 3px;
  /* dịch chuyển form về vị trí đè lên container-parent */
  position: absolute;
  left: 5%;
  right: 5%;
  transform: translate(0%, -50%);
  height: 50%;
  border-radius: 10px;
  /* căn giữa */
  display: flex;
  justify-content: space-around;
  align-items: center;
}
input {
  border: none;
}
/* adjust icon */
.Form-icon {
  display: flex;
  gap: 3px;
  align-items: center;
}
