.Hotel-item-container {
  display: grid;
  grid-template-rows: 2fr 1fr;
}
.Hotel-item-image {
  width: 100%;
  height: 100%;
}
.Hotel-item-image-content {
  color: #7a7575;
  width: 100%;
}
.Hotel-Rating-container {
  display: flex;
  gap: 10px;
}
.Hotel-rate {
  color: white;
  background-color: #2c2c8b;
  padding: 3px;
}
