.Search-Item-name {
  color: rgb(16, 135, 239);
}
.SearchListItem-Parent {
  display: grid;
  grid-template-columns: 1fr 2fr 0.75fr;
  /* gap: 30px; */
  border: 1px solid grey;
  border-radius: 10px;
}
.Search-Hotel-item-image {
  width: 100%;
  height: 100%;
  /* border-bottom-left-radius: 10px; */
  /* border-top-left-radius: 10px; */
}
.SearchListItem {
  /* border: 1px solid blue; */
  /* padding: 20px; */
  margin: 15px;
}
.Search-hotel-tag {
  background-color: rgb(104, 164, 45);
  color: white;
  border-radius: 3px;
  display: inline-block;
  padding: 3px;
}
.Free_cancel {
  color: rgb(104, 164, 45);
}
.Search-Hotel-Rating-container {
  display: flex;
  justify-content: space-between;
}
.Search-Hotel-rate {
  color: white;
  background-color: #2c2c8b;
  padding: 3px;
}
