.navbar-item {
  display: inline-flex;

  margin: 10px;
  height: 15px;
  width: auto;
  padding: 10px;
  align-items: center;
  justify-content: center;
}

.navbar-item.active {
  border: 1px solid rgb(253, 253, 255);
  border-radius: 15px;
}
.navbar-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-link {
  text-decoration: none;
  color: #ffffff;
}
