.SearchPopup-Parent {
  background-color: rgb(237, 193, 84);
  border-radius: 5px;
  height: fit-content;
  margin-left: 100px;
  margin-top: 5px;
  padding-left: 10px;
  padding-right: 10px;
}
.search-popup-option {
  display: flex;
  gap: 20px;
  /* border: 1px blue solid; */
  margin-top: 5px;
  margin-bottom: 5px;
  opacity: 0.5;
}
.search-popup-option-label {
  width: 150px;
}
.search-popup-option-input {
  width: 50px;
}
.SearchPopup-Child {
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 10px;
}
.option-input {
  width: 60px;
  margin-right: 5px;
}
.option-form {
  display: flex;
  flex-direction: column;
  /* border: 2px solid royalblue; */
}
